body {
	margin: 0;
	box-sizing: border-box;
	padding: 0;
}

p {
	padding: 0;
	font-family: Permanent Marker;
	font-size: 1rem;
	color: black;
	cursor: pointer;
}

img {
	cursor: pointer;
}

h2 {
	font-family: Permanent Marker;
	font-size: 18pt;
	color: black;
}

.postIt {
	display: flex;
	flex-direction: column;
	align-items: center;
	max-width: 200px;
	min-width: 150px;
	width: 100%;
	max-height: 200px;
	min-height: 150px;
	height: auto;
	text-decoration: none;
	background-image: url(img/postit.png);
	background-repeat: no-repeat;
	background-size: 100% 100%;
	transition: all 0.2s ease;
}

.postIt:hover {
	transform: scale(1.05);
}

